export default class Item{
    constructor(id, type, name, desc, imgSrc_jpg, imgSrc_png){
        this.id = id;
        this.type = type; // Loi item: o, qun, giy dp,...
        this.name = name;
        this.desc = desc;
        this.imgSrc_jpg = imgSrc_jpg; // hnh show bn khung shopping
        this.imgSrc_png = imgSrc_png; // hnh show trn model
    }
}


